.nav {

    justify-content: space-between;
    align-items: center;
    display: flex;
    padding: 10px;
    margin: auto;
    width: 90%;

    position: relative;

    right: 0px;
    left: 0px;
}

.logo {
    font-weight: bold;
    font-size: 20px;
    gap: 10px;
    display: flex;

    justify-content: center;
    align-items: center;
}

.logoImageWrapper {
    width: 45px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    position: relative;
}

.logoImageWrapper .logoimage {
    height: 100%;
    object-fit: contain;
    display: flex;
    width: auto;
    z-index: 1;
    color: red;
    position: absolute;
}

.logoImageWrapper .logo2 {
    height: 60%;
    width: auto;
    right: -5px;
    bottom: 0px;
    display: flex;
}

.links {
    display: flex;
    text-decoration: none;
    gap: 20px;
    font-weight: bold;
    font-size: 15px;
    color: black;


}